import Link from 'next/link';
import Image from 'next/image';
import CategoryBadge from './CategoryBadge';

interface FeaturedBlogCardProps {
  slug: string;
  title: string;
  excerpt: string;
  coverImage?: string;
  author: {
    name: string;
    avatar?: string;
  };
  publishDate: string;
  category: string;
  readingTime?: string;
}

export default function FeaturedBlogCard({
  slug,
  title,
  excerpt,
  coverImage,
  author,
  publishDate,
  category,
  readingTime,
}: FeaturedBlogCardProps) {
  return (
    <article className="group cursor-pointer mb-12">
      <Link href={`/blog/${slug}`}>
        <div className="relative overflow-hidden rounded-2xl bg-gradient-to-br from-gray-900 to-gray-800">
          {coverImage && (
            <div className="relative w-full h-[500px]">
              <Image
                src={coverImage}
                alt={title}
                fill
                className="object-cover opacity-60 group-hover:opacity-70 transition-opacity duration-500"
              />
              <div className="absolute inset-0 bg-gradient-to-t from-black/90 via-black/30 to-transparent" />
            </div>
          )}
          
          <div className="absolute bottom-0 left-0 right-0 p-8 md:p-12">
            <div className="max-w-3xl">
              <div className="mb-4">
                <CategoryBadge name={category} variant="large" dark />
              </div>
              <h1 className="text-4xl md:text-5xl font-bold mb-4 text-white leading-tight group-hover:text-gray-200 transition-colors">
                {title}
              </h1>
              <p className="text-lg md:text-xl mb-8 text-gray-200 line-clamp-3 leading-relaxed">{excerpt}</p>
              <div className="flex flex-wrap items-center gap-4 text-sm text-gray-300">
                <div className="flex items-center gap-3">
                  <div className="w-10 h-10 rounded-full bg-gradient-to-br from-blue-400 to-purple-500 flex items-center justify-center text-white font-medium">
                    {author.name.charAt(0).toUpperCase()}
                  </div>
                  <div>
                    <div className="font-medium text-white">{author.name}</div>
                  </div>
                </div>
                <span>·</span>
                <span>{publishDate}</span>
                <span>·</span>
                {readingTime && <span>{readingTime} min read</span>}
              </div>
            </div>
          </div>
        </div>
      </Link>
    </article>
  );
}

