import Link from 'next/link';
import Image from 'next/image';
import CategoryBadge from './CategoryBadge';
import AuthorCard from './AuthorCard';

interface BlogCardProps {
  slug: string;
  title: string;
  excerpt: string;
  coverImage?: string;
  author: {
    name: string;
    avatar?: string;
  };
  publishDate: string;
  category: string;
  readingTime?: string;
}

export default function BlogCard({
  slug,
  title,
  excerpt,
  coverImage,
  author,
  publishDate,
  category,
  readingTime,
}: BlogCardProps) {
  return (
    <article className="group cursor-pointer">
      <Link href={`/blog/${slug}`}>
        <div className="flex flex-col sm:flex-row gap-4 py-6 border-b border-gray-100 hover:bg-gray-50/50 transition-colors -mx-4 px-4 rounded-lg">
          {/* Content */}
          <div className="flex-1 min-w-0">
            <div className="flex items-center gap-2 mb-2">
              <CategoryBadge name={category} href={`/categories/${category.toLowerCase().replace(/\s+/g, '-')}`} />
            </div>
            <h2 className="text-xl font-bold text-gray-900 mb-2 group-hover:text-gray-700 transition-colors line-clamp-2 leading-snug">
              {title}
            </h2>
            <p className="text-gray-600 mb-4 line-clamp-2 text-sm leading-relaxed">{excerpt}</p>
            <div className="flex items-center gap-4 text-xs text-gray-500">
              <div className="flex items-center gap-2">
                <div className="w-6 h-6 rounded-full bg-gradient-to-br from-blue-400 to-purple-500 flex items-center justify-center text-white font-medium text-xs">
                  {author.name.charAt(0).toUpperCase()}
                </div>
                <span className="font-medium text-gray-900">{author.name}</span>
              </div>
              <span>{publishDate}</span>
              {readingTime && <span>{readingTime} min read</span>}
            </div>
          </div>
          
          {/* Image */}
          {coverImage && (
            <div className="relative w-full sm:w-32 h-48 sm:h-32 flex-shrink-0 rounded-lg overflow-hidden bg-gray-100">
              <Image
                src={coverImage}
                alt={title}
                fill
                className="object-cover group-hover:scale-105 transition-transform duration-300"
              />
            </div>
          )}
        </div>
      </Link>
    </article>
  );
}

